<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <div class="tab-model">
      <div class="tab-table-div tab-table-left">
        <a-tabs v-model:activeKey="leftActiveKey" @change="handleLeftTabsChange">
          <a-tab-pane key="1" tab="组织">
            <div class="tab-tree-layout">
              <CommonTree
                title="组织树"
                :value="orgTreeData"
                :toolbar="true"
                :canEdit="false"
                :canAdd="false"
                :canDelete="false"
                :isShowOperationBtns="false"
                :fieldNames="{ key: 'id', title: 'name' }"
                @select="handleOrgSelect"
                class="tab-tree"
              />
            </div>
          </a-tab-pane>
          <a-tab-pane key="2" tab="标准岗" force-render>
            <div class="tab-tree-layout">
              <CommonTree
                title="标准岗树"
                :value="posTreeData"
                :toolbar="true"
                :canEdit="false"
                :canAdd="false"
                :canDelete="false"
                :isShowOperationBtns="false"
                :fieldNames="{ key: 'id', title: 'name' }"
                @select="handlePosSelect"
                class="tab-tree"
              />
            </div>
          </a-tab-pane>
        </a-tabs>
      </div>
      <div class="tab-table-div tab-table-right">
        <a-tabs v-model:activeKey="rightActiveKey" @change="handleRightTabsChange">
          <a-tab-pane key="1" tab="角色">
            <BasicTable
              @register="registerRoleTable"
              @resize-column="(w, col) => (col.width = w)"
            />
          </a-tab-pane>
          <a-tab-pane key="2" tab="功能">
            <BasicTable
              @register="registerFunctionTable"
              @resize-column="(w, col) => (col.width = w)"
              :expandIconAsCell="false"
              :expandIconColumnIndex="1"
            />
          </a-tab-pane>
          <a-tab-pane key="3" tab="数据">
            <BasicTable
              @register="registerDataTable"
              @resize-column="(w, col) => (col.width = w)"
            />
          </a-tab-pane>
        </a-tabs>
      </div>
    </div>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { onMounted, reactive, ref } from 'vue';
  import { BasicTable, useTable, SorterResult } from '/@/components/Table';
  import { PageWrapper } from '/@/components/Page';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import { TreeItem } from '@/components/Tree';
  import { getListTree as getOrgTreeData } from '@/api/sys/org';
  import {
    getPosTreeList,
    roleColumns,
    functionColumns,
    dataColumns,
    getRolePage,
    getFunctionPage,
    getDataPage,
    searchRoleForm,
    searchFunctionForm,
    searchDataForm,
  } from './index';

  const searchInfo = reactive<any>({ type: 'org', role: {}, function: {}, data: {} });
  const orgTreeData = ref<TreeItem[]>([]);
  const posTreeData = ref<TreeItem[]>([]);
  const leftActiveKey = ref('1');
  const rightActiveKey = ref('1');

  const getPage = (type: string) => {
    if (type === 'role') {
      return getRolePaginationRef();
    }
    if (type === 'function') {
      return getFunctionPaginationRef();
    }
    if (type === 'data') {
      return getDataPaginationRef();
    }
    return {};
  };

  const getFormValue = (type: string) => {
    if (type === 'role') {
      return getRoleForm().getFieldsValue();
    }
    if (type === 'function') {
      return getFunctionForm().getFieldsValue();
    }
    if (type === 'data') {
      return getDataForm().getFieldsValue();
    }
    return {};
  };

  const getParam = (type: string) => {
    const formValue = getFormValue(type);
    let param = { ...formValue } as any;
    const pageInfo = getPage(type) as any;
    param.pageNo = pageInfo?.current;
    param.pageSize = pageInfo?.pageSize;

    param.orgId = searchInfo.orgId;
    param.posId = searchInfo.posId;
    param.type = searchInfo.type;

    param.sortField = searchInfo[type].sortField;
    param.sortOrder = searchInfo[type].sortOrder;
    return param;
  };

  const loadDataList = async (type) => {
    const param = getParam(type);
    if (type === 'role') {
      return await getRolePage(param);
    }
    if (type === 'function') {
      return await getFunctionPage(param);
    }
    if (type === 'data') {
      return await getDataPage(param);
    }
    return { list: [], total: 0 };
  };

  const sortFn = (sortInfo: SorterResult, type: string) => {
    const sortOrder = sortInfo.order?.replace('end', '');
    searchInfo[type].sortOrder = sortOrder;
    searchInfo[type].sortField = sortOrder ? sortInfo.field : undefined;
  };

  const [
    registerRoleTable,
    { reload: reloadRole, getForm: getRoleForm, getPaginationRef: getRolePaginationRef },
  ] = useTable({
    api: () => loadDataList('role'),
    rowKey: 'id',
    columns: roleColumns,
    useSearchForm: true,
    formConfig: searchRoleForm,
    showTableSetting: false,
    bordered: true,
    sortFn: (sortInfo: SorterResult) => sortFn(sortInfo, 'role'),
  });

  const [
    registerFunctionTable,
    {
      reload: reloadFunction,
      getForm: getFunctionForm,
      getPaginationRef: getFunctionPaginationRef,
    },
  ] = useTable({
    api: () => loadDataList('function'),
    rowKey: 'id',
    columns: functionColumns,
    useSearchForm: true,
    formConfig: searchFunctionForm,
    isTreeTable: true,
    showIndexColumn: false,
    pagination: false,
    showTableSetting: false,
    bordered: true,
    sortFn: (sortInfo: SorterResult) => sortFn(sortInfo, 'function'),
  });

  const [
    registerDataTable,
    { reload: reloadData, getForm: getDataForm, getPaginationRef: getDataPaginationRef },
  ] = useTable({
    api: () => loadDataList('data'),
    rowKey: 'id',
    columns: dataColumns,
    useSearchForm: true,
    formConfig: searchDataForm,
    showTableSetting: false,
    bordered: true,
    sortFn: (sortInfo: SorterResult) => sortFn(sortInfo, 'data'),
  });

  const reload = () => {
    if (rightActiveKey.value === '1') {
      reloadRole();
    }
    if (rightActiveKey.value === '2') {
      reloadFunction();
    }
    if (rightActiveKey.value === '3') {
      reloadData();
    }
  };

  const handleOrgSelect = (node) => {
    searchInfo.type = 'org';
    searchInfo.orgId = null;
    if (node) {
      searchInfo.orgId = node.fullId;
    }
    reload();
  };

  const handleLeftTabsChange = (value) => {
    searchInfo.type = value === '1' ? 'org' : 'pos';
    reload();
  };

  const handleRightTabsChange = () => {
    reload();
  };

  const handlePosSelect = (node) => {
    searchInfo.type = 'pos';
    searchInfo.posId = null;
    if (node) {
      searchInfo.posId = node.id;
    }
    reload();
  };

  const initTreeList = async () => {
    // 组织
    orgTreeData.value = await getOrgTreeData({ orgKindIds: 'ogn,dpt,pos,psm', showPosition: true });
    // 标准岗
    posTreeData.value = (await getPosTreeList()).list;
  };

  // 启动执行代码
  onMounted(() => {
    initTreeList();
  });
</script>
